.header{	
  background-color: #282c34;
  width: 100%;
  height: 100%;
}

.header-button{
	float: right;
	margin: 8px 10px;
  width: 75px;
  height: 30px;
  font-size: 18px;
  background-color: transparent;
  border: 2px solid;
  border-color: rgba(96, 102, 114, 0.6);	
  border-radius: 6px;	
  cursor :pointer;
  user-select: none; 
  font-weight: 500;
}
.header-button:hover{
	background-color: rgb(96, 102, 114);	
}
.logo{
  background-image: url("../../assets/logoVKR.png");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  width: 50px;
  height: 50px;  
}